import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { useEffect, useMemo, useState } from "react";
import { useAuth } from "../hooks/useAuth";

import { Login } from "../screens/auth/Login";
import CalculatorScreen from "./calculator";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { useDispatch } from "react-redux";
import { setCredentials } from "./auth/authSlice";
import { Apps } from "./Apps";
import { HeaderBackground } from "../components/Header";

const Stack = createNativeStackNavigator();

export const Main = () => {
  const auth = useAuth();
  const [loading, setLoading] = useState(true);
  const loggedIn = useMemo(() => auth && auth.user, [auth?.user]);
  const dispatch = useDispatch();

  useEffect(() => {
    const checkIfUserDataExist = async () => {
      try {
        const jsonValue = await AsyncStorage.getItem("@user");
        const data = jsonValue != null ? JSON.parse(jsonValue) : null;

        if (jsonValue) {
          dispatch(setCredentials(data));
        }

        setLoading(false);
      } catch (error) {
        setLoading(false);
      }
    };

    checkIfUserDataExist();
  }, [dispatch, setCredentials]);

  if (loading) {
    return null;
  }

  return (
    <Stack.Navigator initialRouteName="apps">
      {!loggedIn ? (
        <Stack.Screen
          name="login"
          component={Login}
          options={{ header: () => null }}
        />
      ) : (
        <>
          <Stack.Screen
            name="apps"
            component={Apps}
            options={{
              headerBackVisible: false,
              headerTitle: "",
              headerBackground: () => <HeaderBackground />,
            }}
          />
          <Stack.Screen
            name="calculator"
            component={CalculatorScreen}
            options={{
              headerBackVisible: false,
              headerTitle: "",
              headerBackground: () => <HeaderBackground />,
            }}
          />
        </>
      )}
    </Stack.Navigator>
  );
};
