import {
  View,
  TextInput as RNInput,
  Text,
  StyleSheet,
  TextInputProps,
} from "react-native";
import { theme } from "../style/theme";

interface Props extends TextInputProps {
  label?: string;
  error?: string;
}

export const TextInput: React.FC<Props> = ({ label, error, ...rest }) => {
  const style = error ? errorStyle : defaultStyle;
  return (
    <View style={style.containerStyle}>
      {label && <Text style={style.labelStyle}>{label}</Text>}
      <RNInput style={style.inputStyle} {...rest} />
      {error && <Text style={style.errorLabelStyle}>{error}</Text>}
    </View>
  );
};

const defaultStyle = StyleSheet.create({
  containerStyle: {
    width: "100%",
  },
  inputStyle: {
    height: 48,
    paddingLeft: 16,
    borderWidth: 1,
    borderColor: theme.color.grey,
    borderRadius: 4,
  },
  labelStyle: {
    fontSize: 12,
    marginBottom: 4,
  },
  errorLabelStyle: {
    fontSize: 12,
    color: theme.color.red,
    marginTop: 4,
  },
});

const errorStyle = StyleSheet.create({
  containerStyle: {
    width: "100%",
  },
  inputStyle: {
    height: 48,
    paddingLeft: 16,
    borderWidth: 1,
    borderColor: theme.color.red,
    borderRadius: 4,
  },
  labelStyle: {
    fontSize: 12,
    marginBottom: 4,
  },
  errorLabelStyle: {
    fontSize: 12,
    color: theme.color.red,
    marginTop: 4,
  },
});
