import {
  StyleSheet,
  Platform,
  SafeAreaView,
  Text,
  Alert,
  View,
  BackHandler,
} from "react-native";
import { Button } from "../components/Button";
import {
  useFetchSessionsQuery,
  useSessionStartMutation,
  useSessionStopMutation,
} from "../store/services/calculator";
import { Info } from "../components/Info";
import { useAuth } from "../hooks/useAuth";
import { useCalculator } from "../hooks/useCalculator";
import { useDispatch } from "react-redux";
import { setCredentials } from "./auth/authSlice";
import { useEffect, useLayoutEffect } from "react";
import { HeaderRigtButton } from "../components/Header";

export const Apps = ({ navigation }) => {
  const auth = useAuth();
  const calculatorState = useCalculator();
  const dispatch = useDispatch();

  const { isLoading: isSessionsFetching } = useFetchSessionsQuery(
    auth.user?.id,
  );
  const [
    startSession,
    {
      error: sessionStartError,
      isError: isSessionStartError,
      isLoading: isSessionStartLoading,
    },
  ] = useSessionStartMutation();
  const [stopSession] = useSessionStopMutation();

  useLayoutEffect(() => {
    navigation.setOptions({
      headerRight: () => (
        <HeaderRigtButton onPress={changeUser} text="Change user" />
      ),
    });
  }, [navigation, calculatorState]);

  useEffect(() => {
    const handleBackPress = () => {
      Alert.alert(
        "Warning!",
        "Are you sure you want to continue? Your session wil be ended",
        [
          {
            text: "Cancel",
            onPress: () => null,
            style: "cancel",
          },
          {
            text: "OK",
            onPress: () => endSessionAndLogout(),
          },
        ],
      );

      return true;
    };

    const backHandler = BackHandler.addEventListener(
      "hardwareBackPress",
      handleBackPress,
    );

    return () => backHandler.remove();
  }, []);

  const handleStartSession = () => {
    startSession();
  };

  const endSession = async () => {
    if (calculatorState?.activeSession?.status === "STARTED") {
      await stopSession(calculatorState.activeSession.id);
    }
  };

  const endSessionAndLogout = async () => {
    await endSession();
    dispatch(setCredentials({}));
  };

  const changeUser = () =>
    Platform.OS === "web"
      ? endSessionAndLogout()
      : Alert.alert(
          "Warning!",
          "Are you sure you want to continue? Your session wil be ended",
          [
            {
              text: "Cancel",
              onPress: () => null,
              style: "cancel",
            },
            {
              text: "OK",
              onPress: () => endSessionAndLogout(),
            },
          ],
        );

  const openCalculator = () => {
    navigation.push("calculator");
  };

  const renderContent = () => {
    const { activeSession } = calculatorState || {};
    if (isSessionsFetching) {
      return <Text>Loading...</Text>;
    }

    if (!activeSession || !activeSession.is_active) {
      return <Info text="You don't have any active session" />;
    }

    if (activeSession && activeSession.status !== "STARTED") {
      return (
        <Button
          onClick={handleStartSession}
          text={isSessionStartLoading ? "Starting" : "Start session"}
          disabled={isSessionStartLoading}
        />
      );
    }

    return (
      <>
        <Button
          style={style.button}
          text="Open Calculator"
          onClick={openCalculator}
        />
        <Button
          style={style.button}
          text="End session"
          onClick={endSession}
          type="secondary"
        />
      </>
    );
  };

  return (
    <SafeAreaView style={style.safeArea}>
      <View style={style.container}>
        {isSessionStartError && (
          <Info text={sessionStartError?.data?.message} />
        )}
        {renderContent()}
      </View>
    </SafeAreaView>
  );
};

const style = StyleSheet.create({
  safeArea: {
    flex: 1,
    alignItems: "center",
    alignSelf: "center",
    width: "100%",
    height: "100%",
  },
  container: {
    flex: 1,
    alignItems: "center",
    alignSelf: "center",
    padding: 24,
    maxWidth: Platform.OS === "web" ? 400 : "100%",
    width: "100%",
  },
  button: {
    width: "100%",
    marginTop: 24,
  },
});
