import { useLayoutEffect } from "react";
import { StyleSheet, Platform, SafeAreaView, Alert } from "react-native";
import { HeaderBackButton } from "../../components/Header";
import { useCalculateMutation } from "../../store/services/calculator";
import { Calculator } from "./Calculator";

const CalculatorScreen = ({ navigation }) => {
  const [calculate, { isLoading: isCalculateLaoding, data }] =
    useCalculateMutation();

  const handleBack = () => navigation.navigate("apps");

  useLayoutEffect(() => {
    navigation.setOptions({
      headerLeft: () => <HeaderBackButton onBack={handleBack} />,
    });
  }, [navigation]);

  const handleCalculate = async (
    operand1: string,
    operand2: string,
    operator: string,
  ) => {
    await calculate({
      operand1,
      operand2,
      operator,
      client_id: Platform.OS,
    });
  };

  return (
    <SafeAreaView style={style.container}>
      <Calculator
        calculate={handleCalculate}
        loading={isCalculateLaoding}
        result={data?.result}
      />
    </SafeAreaView>
  );
};

const style = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
    alignItems: "center",
    padding: 24,
    marginTop: 48,
  },
});

export default CalculatorScreen;
