import { api } from "./api";
import { convertStringToDouble } from "../../helpers";

export const calculatorApi = api.injectEndpoints({
  endpoints: (build) => ({
    calculate: build.mutation<any, any>({
      query: ({ operand1, operand2, operator, client_id }) => {
        let calculatorUrlPostfix = "";
        switch (operator) {
          case "+":
            calculatorUrlPostfix = "add";
            break;
          case "-":
            calculatorUrlPostfix = "subtract";
            break;
          case "*":
            calculatorUrlPostfix = "multiply";
            break;
          case "/":
            calculatorUrlPostfix = "divide";
            break;
        }
        return {
          url: `api/v1/calculators/${calculatorUrlPostfix}`,
          method: "POST",
          body: {
            number1: convertStringToDouble(operand1),
            number2: convertStringToDouble(operand2),
            client_id,
          },
        };
      },
    }),
    fetchSessions: build.query<any, any>({
      query: (userId) => ({
        url: `/api/v1/students/${userId}/sessions`,
        method: "GET",
      }),
      providesTags: ["session"],
    }),
    sessionStart: build.mutation<any, any>({
      query: () => ({
        url: "api/v1/sessions/start",
        method: "POST",
      }),
    }),
    sessionStop: build.mutation<any, any>({
      query: (sessionId) => ({
        url: `api/v1/sessions/${sessionId}/end`,
        method: "POST",
        body: "",
      }),
      invalidatesTags: ["session"],
    }),
  }),
});

export const {
  useCalculateMutation,
  useFetchSessionsQuery,
  useSessionStartMutation,
  useSessionStopMutation,
} = calculatorApi;
