import React, { useEffect, useState } from "react";
import { View, StyleSheet, Text, TouchableOpacity } from "react-native";

import { theme } from "../../style/theme";

interface CalculatorButtonProps {
  text: string;
  onClick: (text: string) => void;
  style?: any;
  disabled?: boolean;
}

const CalculatorButton: React.FC<CalculatorButtonProps> = ({
  text,
  onClick,
  disabled,
}) => {
  const handleClick = () => {
    onClick(text);
  };
  return (
    <TouchableOpacity
      style={styles.button}
      onPress={handleClick}
      disabled={disabled}
    >
      <Text style={styles.buttonText}>{text}</Text>
    </TouchableOpacity>
  );
};

interface CalculatorProps {
  calculate: (operand1: string, operand2: string, operator: string) => void;
  loading: boolean;
  result: string;
}

export const Calculator: React.FC<CalculatorProps> = ({
  calculate,
  loading,
  result,
}) => {
  const [state, setState] = useState({
    operand1: "",
    operand2: "",
    operator: "",
    result: result ?? "",
  });

  useEffect(() => {
    setState((s) => ({ ...s, result }));
  }, [result]);

  const handlePress = (value: string) => {
    const operand = !state.operator ? "operand1" : "operand2";

    if (value === "AC") {
      setState({
        operand1: "",
        operand2: "",
        operator: "",
        result: "",
      });
    } else if (/[\d,]/g.test(value)) {
      if (/,/g.test(value) && /,/g.test(state[operand])) {
        return;
      }

      setState((current) =>
        current.result
          ? {
              operand1: value,
              operand2: "",
              operator: "",
              result: "",
            }
          : {
              ...current,
              [operand]: state[operand].concat(value),
            },
      );
    } else if (/[/x\-*+]/.test(value)) {
      if (!state.operand1) {
        return;
      }

      setState((current) => ({
        ...current,
        operator: value,
      }));
    } else if (value === "=") {
      if (!loading) {
        calculate(state.operand1, state.operand2, state.operator);
      }
    }
  };

  return (
    <View style={styles.container}>
      <View style={styles.screen}>
        <Text style={styles.screenText}>
          {state.operand1} {state.operator} {state.operand2}
          {state.result ? (
            <Text style={styles.resultText}>
              = {state.result.toString().replace(/\./g, ",")}
            </Text>
          ) : null}
        </Text>
      </View>
      <View style={styles.buttonGroup}>
        <View style={styles.row}>
          <CalculatorButton text="AC" onClick={handlePress} />
          <CalculatorButton text="" onClick={handlePress} disabled={true} />
          <CalculatorButton text="" onClick={handlePress} disabled={true} />
          <CalculatorButton text="/" onClick={handlePress} />
        </View>
        <View style={styles.row}>
          <CalculatorButton text="7" onClick={handlePress} />
          <CalculatorButton text="8" onClick={handlePress} />
          <CalculatorButton text="9" onClick={handlePress} />
          <CalculatorButton text="*" onClick={handlePress} />
        </View>
        <View style={styles.row}>
          <CalculatorButton text="4" onClick={handlePress} />
          <CalculatorButton text="5" onClick={handlePress} />
          <CalculatorButton text="6" onClick={handlePress} />
          <CalculatorButton text="-" onClick={handlePress} />
        </View>
        <View style={styles.row}>
          <CalculatorButton text="1" onClick={handlePress} />
          <CalculatorButton text="2" onClick={handlePress} />
          <CalculatorButton text="3" onClick={handlePress} />
          <CalculatorButton text="+" onClick={handlePress} />
        </View>
        <View style={styles.row}>
          <CalculatorButton text="0" onClick={handlePress} />
          <CalculatorButton text="" onClick={handlePress} disabled={true} />
          <CalculatorButton text="," onClick={handlePress} />
          <CalculatorButton text="=" onClick={handlePress} disabled={loading} />
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    width: "100%",
    marginTop: 24,
    maxWidth: 500,
    height: 330,
  },
  row: {
    display: "flex",
    flex: 4,
    height: 48,
    width: "100%",
    flexDirection: "row",
  },
  buttonGroup: {
    width: "100%",
    height: 5 * 48 + 24,
  },
  button: {
    flex: 1,
    margin: 4,
    height: 48,
    alignItems: "center",
    justifyContent: "center",
    textTransform: "uppercase",
    width: "100%",
    backgroundColor: theme.color.primary,
    borderRadius: 4,
  },
  buttonText: {
    color: "#ffffff",
    fontWeight: "bold",
    fontSize: 16,
  },
  screen: {
    padding: 8,
    justifyContent: "center",
    backgroundColor: "#dbdbdb",
    marginBottom: 16,
    height: 80,
  },
  screenText: {
    fontWeight: "bold",
    fontSize: 24,
    textAlign: "right",
  },
  resultText: {
    fontWeight: "bold",
    fontSize: 28,
    textAlign: "right",
  },
});
