import { createSlice } from "@reduxjs/toolkit";
import { calculatorApi } from "../../store/services/calculator";
import type { RootState } from "../../store/store";

type CalcualtorState = {
  activeSession: unknown;
};

const slice = createSlice({
  name: "calculator",
  initialState: {
    activeSession: undefined,
  } as CalcualtorState,
  reducers: {
    setActiveSession: (state, action) => {
      state.activeSession = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addMatcher(
        calculatorApi.endpoints.sessionStart.matchFulfilled,
        (state, action) => {
          state.activeSession = action.payload;
        },
      )
      .addMatcher(
        calculatorApi.endpoints.fetchSessions.matchFulfilled,
        (state, action) => {
          state.activeSession = action.payload;
        },
      )
      .addMatcher(
        calculatorApi.endpoints.sessionStop.matchFulfilled,
        (state) => {
          state.activeSession = undefined;
        },
      );
  },
});

export const { setActiveSession } = slice.actions;

export default slice.reducer;

export const getCalculatorState = (state: RootState) => state.calculator;
