import { View, StyleSheet, Image, Platform } from "react-native";
import { useState } from "react";
import AppLogo from "../../../assets/app-logo.png";
import { useLoginMutation } from "../../store/services/auth";
import { Info } from "../../components/Info";
import { TextInput } from "../../components/TextInput";
import { Button } from "../../components/Button";
import { FormField } from "../../components/FormField";

interface LoginFormState {
  username: string;
  password: string;
}

export const Login = ({ navigation }) => {
  const [formState, setFormState] = useState<LoginFormState>({
    username: "",
    password: "",
  });
  const [login, data] = useLoginMutation();

  const setUsername = (value: string) => {
    setFormState((state) => ({ ...state, username: value }));
  };

  const setPassword = (value: string) => {
    setFormState((state) => ({ ...state, password: value }));
  };

  const handleLogin = async () => {
    await login({
      username: formState.username,
      password: formState.password,
    }).unwrap();

    navigation.replace("apps");
  };

  return (
    <View style={style.container}>
      <Image
        source={AppLogo}
        style={{ width: 200, height: 60, marginBottom: 48 }}
      />
      {data.isError && <Info text={data.error?.data?.message} />}

      <FormField>
        <TextInput
          placeholder="Username"
          label="Enter username*"
          value={formState.username}
          onChangeText={setUsername}
          secureTextEntry={false}
        />
      </FormField>
      <FormField>
        <TextInput
          placeholder="Password"
          label="Enter password*"
          secureTextEntry={true}
          value={formState.password}
          onChangeText={setPassword}
        />
      </FormField>
      <FormField>
        <Button text="Login" onClick={handleLogin} disabled={data.isLoading} />
      </FormField>
    </View>
  );
};

const style = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "center",
    padding: 24,
    ...Platform.select({
      web: {
        width: 400,
      },
      default: "100%",
    }),
  },
  loginButton: {
    width: "100%",
  },
});
