import {
  fetchBaseQuery,
  FetchArgs,
  FetchBaseQueryError,
  BaseQueryFn,
} from "@reduxjs/toolkit/query/react";

import Constants from "expo-constants";
import { setCredentials } from "../../screens/auth/authSlice";
import jwtDecode from "jwt-decode";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { RootState } from "../store";

const baseQuery = fetchBaseQuery({
  baseUrl: Constants.manifest?.extra?.baseUrl,
  headers: {
    "content-type": "application/json",
  },
});

const baseQueryWithReauth: BaseQueryFn<
  string | FetchArgs,
  unknown,
  FetchBaseQueryError
> = async (args, api, extraOptions) => {
  const accessToken = (api.getState() as RootState).auth.access_token;
  const refreshToken = (api.getState() as RootState).auth.refresh_token;

  //original request
  let result = await baseQuery(
    {
      ...args,
      headers: {
        authorization: `Bearer ${accessToken}`,
      },
    },
    api,
    extraOptions,
  );

  if (
    result.error &&
    result.error.status === 403 &&
    (result.error.data.error_code === "TOKEN_EXPIRED" ||
      result.error.data.error_code === "TOKEN_INVALID")
  ) {
    // try to get a new token
    const refreshResult = await baseQuery(
      {
        url: "api/v1/refresh-token",
        method: "POST",
        headers: {
          authorization: `Bearer ${refreshToken}`,
        },
      },
      api,
      extraOptions,
    );

    if (refreshResult.data) {
      // store the new token
      const { access_token, refresh_token } = refreshResult.data;
      const userData = {
        user: jwtDecode(access_token),
        access_token,
        refresh_token,
      };

      api.dispatch(setCredentials(userData));
      const jsonValue = JSON.stringify(userData);
      await AsyncStorage.setItem("@user", jsonValue);

      // retry the initial query
      result = await baseQuery(
        {
          ...args,
          headers: {
            authorization: `Bearer ${access_token}`,
          },
        },
        api,
        extraOptions,
      );
    } else {
      api.dispatch(setCredentials({}));
      AsyncStorage.removeItem("@user");
    }
  }
  return result;
};

export default baseQueryWithReauth;
